html, body
  margin: 0
  padding: 0
  text-align: center
  height: 100%
  overflow: hidden

body
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab)
  background-size: 400% 400%
  animation: gradient 15s ease infinite
  font-family: 'Fira Code', monospace
  font-size: 100%
  position: relative
  display: flex
  *
    -webkit-tap-highlight-color: transparent

#canvas
  // 9:16 aspect ratio
  --w: calc(min(100vw,(9/16)*100vh))
  width: var(--w) 
  margin: auto


.splash
  cursor: pointer
  width: 100%
  height: 100%
  filter: drop-shadow(10px 10px 5px black)
  img
    width: 100%
    display: block
    margin: 0 auto

h3  
  font-size: calc(var(--w)/10)
p
  font-size: calc(var(--w)*0.05)
h3,p
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000
  color: white
  margin: 0.2em

.draggable
  filter: drop-shadow(6px 6px 5px black)
  border-radius: calc(var(--w)/100)
  margin: calc(var(--w)*0.01) 0
  text-transform: uppercase
  text-align: center
  width: calc(var(--w))
  background: #e7e7e7
  border: 4px solid transparent
  display: inline-block
  height: calc(var(--w)/10)
  font-size: calc(var(--w)*0.03)
  line-height: calc(var(--w)/10)
  cursor: pointer
  transition: background 0.5s linear
  &.draggable--over
  //&.draggable-source--placed
  //  background-color: #e73c7e
  &.correct
    ///border-color: #3ce755 
    background-color: #ee7752


@keyframes gradient
  0%
    background-position: 0% 50%
  50%
    background-position: 100% 50%
  100%
    background-position: 0% 50%
